<template>
  <div class="app__read">
    <h1>{{ getDate }}</h1>
    <div class="read__empty" v-if="!isEmpty">Отпеваний нет</div>
    <div
      class="read__service"
      v-for="(service, key) in getFunerals"
      :key="service.id"
    >
      <h2>{{ key }}</h2>
      <the-swiper
        @swipe="(e) => swipeEmit(e, funeral.id)"
        v-for="funeral in service"
        :key="funeral.id"
      >
        <read-point :funeral="funeral" />
      </the-swiper>
    </div>
  </div>
</template>

<script>
import ReadPoint from "@/components/AppRead/ReadPoint.vue";
import TheSwiper from "@/components/ui/TheSwiper.vue";

export default {
  name: "AppFunRead",
  components: { ReadPoint, TheSwiper },
  computed: {
    getDate() {
      this.$store.dispatch("SET_FUNERALS_READ");
      return this.$store.getters.GET_DATE_TODAY_DATE;
    },
    isEmpty() {
      return !!this.$store.getters.GET_FUNERALS[0];
    },
    getFunerals() {
      let funerals = this.$store.getters.GET_FUNERALS;

      const serv = funerals.reduce((group, funeral) => {
        const { service } = funeral;
        group[service] = group[service] ?? [];
        group[service].push(funeral);
        return group;
      }, {});
      return serv;
    },
  },
  methods: {
    swipeEmit(newVal, id) {
      if (newVal == "delete") {
        this.$store.dispatch("DELETE_FUNERALS", id);
      }
      if (newVal == "edit") {
        this.$router.replace({ path: `/edit/${id}` });
      }
    },
  },
};
</script>

<style lang="scss">
.app__read {
  width: 100%;
  height: calc(100dvh - 16dvh);
  overflow: auto;
}
.read__empty {
  width: 100%;
  height: 100%;
  height: calc(100dvh - 24dvh);
  line-height: calc(100dvh - 24dvh);
  text-align: center;
  font-weight: 500;
  font-size: 2em;
  color: $col-basic;
  text-transform: uppercase;
}
</style>